import React, { useState, useEffect } from 'react';
import styled, { keyframes } from 'styled-components';
import { FileManager } from '../../utils/fileManager';

// Animaciones
const fadeIn = keyframes`
  from {
    opacity: 0;
    transform: scale(0.9);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
`;

const slideIn = keyframes`
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
`;

// Estilos
const ModalOverlay = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10000;
  animation: ${fadeIn} 0.3s ease-out;
`;

const ModalContainer = styled.div`
  background: var(--color-surface);
  border-radius: 20px;
  border: 2px solid var(--color-primary);
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.4);
  width: 90%;
  max-width: 900px;
  max-height: 80vh;
  overflow: hidden;
  animation: ${slideIn} 0.4s ease-out;
  position: relative;
`;

const ModalHeader = styled.div`
  background: linear-gradient(135deg, var(--color-primary) 0%, var(--color-primaryHover) 100%);
  color: white;
  padding: 20px 24px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 2px solid var(--color-primary);
`;

const ModalTitle = styled.h3`
  margin: 0;
  font-size: 20px;
  font-weight: 700;
  display: flex;
  align-items: center;
  gap: 12px;
  color: #ffffff;
  text-shadow: 0 2px 8px rgba(0, 0, 0, 0.8);
`;

const CloseButton = styled.button`
  background: rgba(255, 255, 255, 0.2);
  border: none;
  color: white;
  width: 36px;
  height: 36px;
  border-radius: 50%;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  font-weight: bold;
  transition: all 0.3s ease;

  &:hover {
    background: rgba(255, 255, 255, 0.3);
    transform: scale(1.1);
  }
`;

const ModalContent = styled.div`
  padding: 24px;
  max-height: 60vh;
  overflow-y: auto;
`;

const TabContainer = styled.div`
  display: flex;
  margin-bottom: 20px;
  border-bottom: 2px solid var(--color-border);
`;

const Tab = styled.button`
  padding: 12px 24px;
  border: none;
  background: ${props => props.active ? 'var(--color-primary)' : 'transparent'};
  color: ${props => props.active ? 'white' : 'var(--color-textPrimary)'};
  cursor: pointer;
  font-weight: 600;
  font-size: 14px;
  border-radius: 8px 8px 0 0;
  transition: all 0.3s ease;
  margin-right: 4px;

  &:hover {
    background: ${props => props.active ? 'var(--color-primaryHover)' : 'var(--color-backgroundSecondary)'};
  }
`;

const ColorGrid = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(120px, 1fr));
  gap: 12px;
  margin-bottom: 20px;
`;

const ColorCard = styled.div`
  background: ${props => props.color};
  border: 3px solid ${props => props.selected ? 'var(--color-primary)' : 'var(--color-border)'};
  border-radius: 12px;
  height: 80px;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  box-shadow: ${props => props.selected ? '0 8px 20px rgba(74, 158, 255, 0.3)' : '0 2px 8px rgba(0, 0, 0, 0.1)'};

  &:hover {
    transform: translateY(-2px);
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  }
`;

const ColorName = styled.div`
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.7);
  color: white;
  padding: 4px 8px;
  font-size: 11px;
  font-weight: 600;
  text-align: center;
  border-radius: 0 0 9px 9px;
`;

const CustomColorContainer = styled.div`
  margin-top: 20px;
  padding: 16px;
  background: var(--color-backgroundSecondary);
  border-radius: 12px;
  border: 2px solid var(--color-border);
`;

const ColorInput = styled.input`
  width: 100%;
  height: 40px;
  border: 2px solid var(--color-border);
  border-radius: 8px;
  background: var(--color-surface);
  cursor: pointer;
  margin-bottom: 12px;

  &::-webkit-color-swatch-wrapper {
    padding: 0;
  }

  &::-webkit-color-swatch {
    border: none;
    border-radius: 6px;
  }
`;

const ColorPreview = styled.div`
  width: 100%;
  height: 60px;
  background: ${props => props.color || '#ffffff'};
  border: 2px solid var(--color-border);
  border-radius: 8px;
  margin-bottom: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: ${props => {
    // Calcular contraste para el texto
    const hex = props.color?.replace('#', '') || 'ffffff';
    const r = parseInt(hex.substr(0, 2), 16);
    const g = parseInt(hex.substr(2, 2), 16);
    const b = parseInt(hex.substr(4, 2), 16);
    const brightness = (r * 299 + g * 587 + b * 114) / 1000;
    return brightness > 128 ? '#000000' : '#ffffff';
  }};
  font-weight: 600;
  font-size: 14px;
`;

const DescriptionText = styled.div`
  background: var(--color-backgroundSecondary);
  border: 1px solid var(--color-border);
  border-radius: 8px;
  padding: 12px 16px;
  margin-bottom: 20px;
  font-size: 13px;
  color: var(--color-textSecondary);
  line-height: 1.4;
  
  strong {
    color: var(--color-textPrimary);
    font-weight: 600;
  }
`;

const SearchContainer = styled.div`
  margin-bottom: 20px;
  position: relative;
`;

const SearchInput = styled.input`
  width: 100%;
  padding: 12px 16px 12px 44px;
  background: var(--color-backgroundSecondary);
  border: 2px solid var(--color-border);
  border-radius: 12px;
  color: var(--color-textPrimary);
  font-size: 14px;
  transition: all 0.3s ease;

  &:focus {
    outline: none;
    border-color: var(--color-primary);
    box-shadow: 0 0 0 3px rgba(74, 158, 255, 0.1);
  }
`;

const SearchIcon = styled.div`
  position: absolute;
  left: 16px;
  top: 50%;
  transform: translateY(-50%);
  color: var(--color-textSecondary);
  font-size: 16px;
`;

const GridContainer = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 16px;
  margin-bottom: 20px;
`;

const BackgroundCard = styled.div`
  background: var(--color-surfaceSecondary);
  border: 2px solid ${props => props.selected ? 'var(--color-primary)' : 'var(--color-border)'};
  border-radius: 12px;
  overflow: hidden;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  box-shadow: ${props => props.selected ? '0 8px 20px rgba(74, 158, 255, 0.3)' : '0 2px 8px rgba(0, 0, 0, 0.1)'};

  &:hover {
    transform: translateY(-4px);
    box-shadow: 0 12px 24px rgba(0, 0, 0, 0.2);
    border-color: ${props => props.selected ? 'var(--color-primary)' : 'var(--color-borderSecondary)'};
  }
`;

const BackgroundImage = styled.div`
  width: 100%;
  height: 120px;
  background-image: url(${props => props.src});
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  position: relative;
  
  &::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(180deg, transparent 0%, rgba(0, 0, 0, 0.3) 100%);
  }
`;

const ImagePreview = styled.img`
  width: 100%;
  height: 120px;
  object-fit: cover;
  object-position: center;
  display: block;
`;

const BackgroundInfo = styled.div`
  padding: 12px;
`;

const BackgroundName = styled.div`
  font-size: 13px;
  font-weight: 600;
  color: var(--color-textPrimary);
  margin-bottom: 4px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
`;

const BackgroundPath = styled.div`
  font-size: 11px;
  color: var(--color-textSecondary);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
`;

const SelectedIndicator = styled.div`
  position: absolute;
  top: 8px;
  right: 8px;
  background: var(--color-primary);
  color: white;
  width: 24px;
  height: 24px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 12px;
  font-weight: bold;
  box-shadow: 0 2px 8px rgba(74, 158, 255, 0.4);
`;

const UploadArea = styled.div`
  border: 2px dashed var(--color-border);
  border-radius: 12px;
  padding: 24px;
  text-align: center;
  cursor: pointer;
  transition: all 0.3s ease;
  background: var(--color-backgroundSecondary);
  margin-bottom: 20px;

  &:hover {
    border-color: var(--color-primary);
    background: var(--color-surfaceSecondary);
  }
`;

const UploadIcon = styled.div`
  font-size: 32px;
  margin-bottom: 12px;
  color: var(--color-primary);
`;

const UploadText = styled.div`
  font-size: 14px;
  color: var(--color-textPrimary);
  font-weight: 600;
  margin-bottom: 4px;
`;

const UploadSubtext = styled.div`
  font-size: 12px;
  color: var(--color-textSecondary);
`;

const ModalActions = styled.div`
  padding: 20px 24px;
  border-top: 1px solid var(--color-border);
  display: flex;
  gap: 12px;
  justify-content: flex-end;
  background: var(--color-backgroundSecondary);
`;

const ActionButton = styled.button`
  padding: 12px 24px;
  border: 2px solid ${props => props.primary ? 'var(--color-primary)' : 'var(--color-border)'};
  border-radius: 10px;
  background: ${props => props.primary 
    ? 'linear-gradient(135deg, var(--color-primary) 0%, var(--color-primaryHover) 100%)' 
    : 'var(--color-surface)'};
  color: ${props => props.primary ? 'white' : 'var(--color-textPrimary)'};
  cursor: pointer;
  font-weight: 600;
  transition: all 0.3s ease;
  font-size: 14px;

  &:hover {
    transform: translateY(-2px);
    box-shadow: 0 6px 16px ${props => props.primary 
      ? 'rgba(74, 158, 255, 0.3)' 
      : 'rgba(0, 0, 0, 0.1)'};
  }

  &:active {
    transform: translateY(0);
  }

  &:disabled {
    opacity: 0.5;
    cursor: not-allowed;
    transform: none;
  }
`;

const EmptyState = styled.div`
  text-align: center;
  padding: 40px 20px;
  color: var(--color-textSecondary);
`;

const EmptyIcon = styled.div`
  font-size: 48px;
  margin-bottom: 16px;
  opacity: 0.5;
`;

const EmptyTitle = styled.h3`
  margin: 0 0 8px 0;
  font-size: 18px;
  color: var(--color-textPrimary);
`;

const EmptyText = styled.p`
  margin: 0;
  font-size: 14px;
  line-height: 1.5;
`;

function BackgroundSelectorModal({ isOpen, onClose, onSelect, currentBackground = '' }) {
  const [backgrounds, setBackgrounds] = useState([]);
  const [loading, setLoading] = useState(false);
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedBackground, setSelectedBackground] = useState(currentBackground);
  const [uploading, setUploading] = useState(false);
  const [activeTab, setActiveTab] = useState('images');
  const [selectedColor, setSelectedColor] = useState('');
  const [customColor, setCustomColor] = useState('#ffffff');

  // Colores predefinidos
  const predefinedColors = [
    { name: 'Blanco', color: '#ffffff' },
    { name: 'Negro', color: '#000000' },
    { name: 'Rojo', color: '#ff0000' },
    { name: 'Verde', color: '#00ff00' },
    { name: 'Azul', color: '#0000ff' },
    { name: 'Amarillo', color: '#ffff00' },
    { name: 'Cian', color: '#00ffff' },
    { name: 'Magenta', color: '#ff00ff' },
    { name: 'Naranja', color: '#ffa500' },
    { name: 'Rosa', color: '#ffc0cb' },
    { name: 'Marrón', color: '#8b4513' },
    { name: 'Gris', color: '#808080' },
    { name: 'Azul Oscuro', color: '#000080' },
    { name: 'Verde Oscuro', color: '#008000' },
    { name: 'Rojo Oscuro', color: '#800000' },
    { name: 'Púrpura', color: '#800080' }
  ];

  // Cargar fondos disponibles
  useEffect(() => {
    if (isOpen) {
      loadBackgrounds();
    }
  }, [isOpen]);

  const loadBackgrounds = async () => {
    setLoading(true);
    try {
      const fileManager = new FileManager();
      const files = await fileManager.getFilesByCategory('backgrounds');
      
      // Procesar las rutas para asegurar que sean accesibles
      const processedFiles = (files || []).map(file => {
        let processedPath = file.path;
        
        // Normalizar la ruta
        if (!processedPath.startsWith('/')) {
          processedPath = `/${processedPath}`;
        }
        
        // Remover dobles barras
        processedPath = processedPath.replace(/\/+/g, '/');
        
        // Usar la URL de datos si está disponible, sino usar la ruta procesada
        const previewUrl = file.url || file.dataUrl || processedPath;
        
        return {
          ...file,
          path: processedPath,
          previewUrl: previewUrl
        };
      });
      
      setBackgrounds(processedFiles);
    } catch (error) {
      console.error('Error al cargar fondos:', error);
    } finally {
      setLoading(false);
    }
  };

  // Filtrar fondos por término de búsqueda
  const filteredBackgrounds = backgrounds.filter(background =>
    background.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
    background.path.toLowerCase().includes(searchTerm.toLowerCase())
  );

  // Manejar selección de fondo
  const handleSelect = (background) => {
    setSelectedBackground(background.path);
    setSelectedColor(''); // Limpiar selección de color
  };

  // Manejar selección de color
  const handleColorSelect = (color) => {
    setSelectedColor(color);
    setSelectedBackground(''); // Limpiar selección de imagen
  };

  // Manejar color personalizado
  const handleCustomColorChange = (color) => {
    setCustomColor(color);
    setSelectedColor(color);
    setSelectedBackground(''); // Limpiar selección de imagen
  };

  // Manejar confirmación
  const handleConfirm = () => {
    if (selectedBackground) {
      onSelect(selectedBackground);
      onClose();
    } else if (selectedColor) {
      // Crear un identificador especial para colores
      const colorBackground = `color:${selectedColor}`;
      onSelect(colorBackground);
      onClose();
    }
  };

  // Manejar subida de archivo
  const handleFileUpload = async (event) => {
    const file = event.target.files[0];
    if (!file) return;

    setUploading(true);
    try {
      const fileManager = new FileManager();
      const result = await fileManager.uploadFile(file, 'backgrounds');
      
      if (result && result.path) {
        // Recargar la lista de fondos
        await loadBackgrounds();
        // Seleccionar el nuevo fondo
        setSelectedBackground(result.path);
      }
    } catch (error) {
      console.error('Error al subir archivo:', error);
    } finally {
      setUploading(false);
    }
  };

  // Manejar cierre con Escape
  useEffect(() => {
    const handleKeyDown = (event) => {
      if (event.key === 'Escape' && isOpen) {
        onClose();
      }
    };

    if (isOpen) {
      document.addEventListener('keydown', handleKeyDown);
      document.body.style.overflow = 'hidden';
    }

    return () => {
      document.removeEventListener('keydown', handleKeyDown);
      document.body.style.overflow = 'unset';
    };
  }, [isOpen, onClose]);

  if (!isOpen) return null;

  return (
    <ModalOverlay onClick={onClose}>
      <ModalContainer onClick={(e) => e.stopPropagation()}>
        <ModalHeader>
          <ModalTitle>
            🖼️ Seleccionar Fondo
          </ModalTitle>
          <CloseButton onClick={onClose}>
            ×
          </CloseButton>
        </ModalHeader>

        <ModalContent>
          {/* Pestañas */}
          <TabContainer>
            <Tab 
              active={activeTab === 'images'} 
              onClick={() => setActiveTab('images')}
            >
              🖼️ Imágenes
            </Tab>
            <Tab 
              active={activeTab === 'colors'} 
              onClick={() => setActiveTab('colors')}
            >
              🎨 Colores
            </Tab>
          </TabContainer>

          {/* Descripción informativa */}
          <DescriptionText>
            <strong>💡 Tip:</strong> Puedes usar tanto <strong>imágenes</strong> como <strong>colores sólidos</strong> para tus fondos. 
            Los colores son perfectos para fondos simples y se generan automáticamente en Ren'Py.
          </DescriptionText>

          {/* Contenido de pestaña de imágenes */}
          {activeTab === 'images' && (
            <>
              {/* Descripción específica para imágenes */}
              <div style={{
                background: 'var(--color-primary)',
                color: 'white',
                padding: '12px 16px',
                borderRadius: '8px',
                marginBottom: '20px',
                fontSize: '13px',
                lineHeight: '1.4'
              }}>
                <strong>🖼️ Fondos de Imágenes:</strong> Sube tus propias imágenes o selecciona de las disponibles. 
                Las imágenes se almacenan en el proyecto y se exportan automáticamente a Ren'Py.
              </div>

              {/* Área de subida */}
              <UploadArea onClick={() => document.getElementById('file-upload').click()}>
                <input
                  id="file-upload"
                  type="file"
                  accept="image/*"
                  onChange={handleFileUpload}
                  style={{ display: 'none' }}
                  disabled={uploading}
                />
                <UploadIcon>
                  {uploading ? '⏳' : '📁'}
                </UploadIcon>
                <UploadText>
                  {uploading ? 'Subiendo archivo...' : 'Subir nuevo fondo'}
                </UploadText>
                <UploadSubtext>
                  Haz clic aquí o arrastra una imagen
                </UploadSubtext>
              </UploadArea>

              {/* Búsqueda */}
              <SearchContainer>
                <SearchIcon>🔍</SearchIcon>
                <SearchInput
                  type="text"
                  placeholder="Buscar fondos..."
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                />
              </SearchContainer>
            </>
          )}

          {/* Contenido de pestaña de colores */}
          {activeTab === 'colors' && (
            <>
              {/* Descripción específica para colores */}
              <div style={{
                background: 'var(--color-primary)',
                color: 'white',
                padding: '12px 16px',
                borderRadius: '8px',
                marginBottom: '20px',
                fontSize: '13px',
                lineHeight: '1.4'
              }}>
                <strong>🎨 Fondos de Colores:</strong> Selecciona un color predefinido o crea uno personalizado. 
                Los fondos de colores son ideales para escenas simples y se exportan automáticamente a Ren'Py.
              </div>

              {/* Colores predefinidos */}
              <ColorGrid>
                {predefinedColors.map((colorObj, index) => (
                  <ColorCard
                    key={`color-${index}`}
                    color={colorObj.color}
                    selected={selectedColor === colorObj.color}
                    onClick={() => handleColorSelect(colorObj.color)}
                  >
                    <ColorName>{colorObj.name}</ColorName>
                  </ColorCard>
                ))}
              </ColorGrid>

              {/* Color personalizado */}
              <CustomColorContainer>
                <div style={{ 
                  fontSize: '14px', 
                  fontWeight: '600', 
                  color: 'var(--color-textPrimary)',
                  marginBottom: '12px'
                }}>
                  Color Personalizado
                </div>
                <ColorInput
                  type="color"
                  value={customColor}
                  onChange={(e) => handleCustomColorChange(e.target.value)}
                />
                <ColorPreview color={customColor}>
                  {customColor.toUpperCase()}
                </ColorPreview>
              </CustomColorContainer>
            </>
          )}


          {/* Grid de fondos - Solo para pestaña de imágenes */}
          {activeTab === 'images' && (
            <>
              {loading ? (
                <EmptyState>
                  <EmptyIcon>⏳</EmptyIcon>
                  <EmptyTitle>Cargando fondos...</EmptyTitle>
                  <EmptyText>Por favor espera mientras cargamos tus fondos disponibles</EmptyText>
                </EmptyState>
              ) : filteredBackgrounds.length === 0 ? (
                <EmptyState>
                  <EmptyIcon>🖼️</EmptyIcon>
                  <EmptyTitle>
                    {searchTerm ? 'No se encontraron fondos' : 'No hay fondos disponibles'}
                  </EmptyTitle>
                  <EmptyText>
                    {searchTerm 
                      ? 'Intenta con otro término de búsqueda'
                      : 'Sube algunos fondos para comenzar a crear tu novela visual'
                    }
                  </EmptyText>
                </EmptyState>
              ) : (
                <GridContainer>
                  {filteredBackgrounds.map((background, index) => (
                    <BackgroundCard
                      key={`bg-${background.path}-${index}`}
                      selected={selectedBackground === background.path}
                      onClick={() => handleSelect(background)}
                    >
                      <div style={{ position: 'relative', height: '120px' }}>
                        {/* Indicador de carga */}
                        <div 
                          style={{
                            position: 'absolute',
                            top: 0,
                            left: 0,
                            right: 0,
                            bottom: 0,
                            background: 'var(--color-backgroundSecondary)',
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'center',
                            flexDirection: 'column',
                            color: 'var(--color-textSecondary)',
                            fontSize: '12px',
                            zIndex: 1
                          }}
                          className="loading-placeholder"
                        >
                          <div style={{ fontSize: '24px', marginBottom: '8px' }}>⏳</div>
                          <div>Cargando...</div>
                        </div>
                        
                        <ImagePreview 
                          src={background.previewUrl || background.url || background.path} 
                          alt={background.name}
                          onLoad={(e) => {
                            // Ocultar placeholder cuando la imagen se carga
                            const placeholder = e.target.parentElement.querySelector('.loading-placeholder');
                            if (placeholder) {
                              placeholder.style.display = 'none';
                            }
                          }}
                          onError={(e) => {
                            // Intentar cargar desde la URL de datos si está disponible
                            if (background.url && e.target.src !== background.url) {
                              e.target.src = background.url;
                              return;
                            }
                            
                            // Si la imagen falla, mostrar un placeholder de error
                            const placeholder = e.target.parentElement.querySelector('.loading-placeholder');
                            if (placeholder) {
                              placeholder.innerHTML = `
                                <div style="font-size: 24px; margin-bottom: 8px;">🖼️</div>
                                <div>Imagen no disponible</div>
                              `;
                              placeholder.style.display = 'flex';
                            }
                            e.target.style.display = 'none';
                          }}
                        />
                        
                        {/* Overlay de gradiente */}
                        <div 
                          style={{
                            position: 'absolute',
                            top: 0,
                            left: 0,
                            right: 0,
                            bottom: 0,
                            background: 'linear-gradient(180deg, transparent 0%, rgba(0, 0, 0, 0.3) 100%)',
                            pointerEvents: 'none',
                            zIndex: 2
                          }}
                        />
                      </div>
                      {selectedBackground === background.path && (
                        <SelectedIndicator>✓</SelectedIndicator>
                      )}
                      <BackgroundInfo>
                        <BackgroundName>{background.name}</BackgroundName>
                        <BackgroundPath>{background.path}</BackgroundPath>
                      </BackgroundInfo>
                    </BackgroundCard>
                  ))}
                </GridContainer>
              )}
            </>
          )}
        </ModalContent>

        <ModalActions>
          <ActionButton onClick={onClose}>
            Cancelar
          </ActionButton>
          <ActionButton 
            primary 
            onClick={handleConfirm}
            disabled={!selectedBackground && !selectedColor}
          >
            {selectedBackground || selectedColor ? 
              (activeTab === 'colors' ? 'Seleccionar Color' : 'Seleccionar Fondo') : 
              (activeTab === 'colors' ? 'Selecciona un color' : 'Selecciona un fondo')
            }
          </ActionButton>
        </ModalActions>
      </ModalContainer>
    </ModalOverlay>
  );
}

export default BackgroundSelectorModal;
