// Configuración del botón flotante de Discord
export const DISCORD_CONFIG = {
  // URL del servidor de Discord (reemplaza con tu URL real)
  serverUrl: 'https://discord.gg/6CFXHuKckz',
  
  // Configuración del botón
  button: {
    // Posición del botón
    position: {
      bottom: '20px',
      right: '20px'
    },
    
    // Tamaño del botón
    size: {
      width: '60px',
      height: '60px'
    },
    
    // Colores del botón
    colors: {
      primary: '#7289da',
      secondary: '#5865f2',
      hover: '#5b6eae'
    },
    
    // Animaciones
    animations: {
      // Mostrar animación de rebote al cargar
      showBounce: true,
      // Mostrar pulso después de 5 segundos
      showPulse: true,
      // Duración del pulso en segundos
      pulseDelay: 5000
    }
  },
  
  // Configuración del modal
  modal: {
    title: '¡Únete a nuestra Comunidad!',
    description: 'Conéctate con otros desarrolladores de novelas visuales, comparte tus proyectos, obtén ayuda y participa en eventos exclusivos de la comunidad RynEditor.',
    buttonText: '¡Unirse a Discord!',
    cancelText: 'Cancelar'
  },
  
  // Configuración del tooltip
  tooltip: {
    text: '¡Únete a nuestro Discord!',
    showDelay: 0,
    hideDelay: 0
  },
  
  // Configuración de notificaciones (opcional)
  notifications: {
    // Mostrar badge de notificación
    showBadge: false,
    // Número de notificaciones (si showBadge es true)
    count: 0,
    // Color del badge
    badgeColor: '#ff4757'
  },
  
  // Configuración de visibilidad
  visibility: {
    // Mostrar solo en ciertas páginas (array de rutas)
    showOnPages: [], // Vacío = mostrar en todas las páginas
    // Ocultar en ciertas páginas
    hideOnPages: ['intro'], // Ocultar en la pantalla de introducción
    // Mostrar solo después de cierto tiempo (en milisegundos)
    showAfterDelay: 1000
  }
};

// Función para obtener la configuración
export const getDiscordConfig = () => {
  return DISCORD_CONFIG;
};

// Función para actualizar la URL del servidor
export const updateDiscordServerUrl = (newUrl) => {
  DISCORD_CONFIG.serverUrl = newUrl;
};

// Función para verificar si el botón debe mostrarse en la página actual
export const shouldShowDiscordButton = (currentPage) => {
  const { visibility } = DISCORD_CONFIG;
  
  // Si hay páginas específicas donde mostrar
  if (visibility.showOnPages.length > 0) {
    return visibility.showOnPages.includes(currentPage);
  }
  
  // Si hay páginas donde ocultar
  if (visibility.hideOnPages.length > 0) {
    return !visibility.hideOnPages.includes(currentPage);
  }
  
  // Mostrar por defecto
  return true;
};
